-left"}},[_vm._v("En el marco de la economía solidaria, la presentación e interpretación de informes no debe verse solo como una práctica contable, sino como un ejercicio de participación democrática. La Ley 79 de 1988 promueve la rendición de cuentas periódica y el acceso equitativo a la información financiera como mecanismos para garantizar la confianza y la responsabilidad colectiva. Por tanto, los informes financieros bien presentados e interpretados son herramientas que fortalecen la gestión institucional, alinean las decisiones con los principios solidarios y fomentan una cultura organizacional basada en la transparencia y el bien común.")])],1)],1)
